.formsec{
display:flex;
flex-direction: row;
background-color: #ffffff;
max-width: 230px;
min-width: 210px;
max-height: 100px;

}

.infogoal{
  display:flex;
  flex-direction: row;
  border: 1px solid rgba(221, 221, 221, 1);
  border-radius: 5px;   
  background-color: #ccfd9b;
  padding: 2px 2px;
  margin: 10px 5px;
  
}
.info{
  background: linear-gradient(90deg, #9ee10b, #bce30f);
  border-radius: 4px;
  border: none;
  cursor: pointer;
  margin-left: 3px; 
  margin-bottom:3px ;
  margin-top: 1px;
  padding: 3px;
}


.setgoal{
  background-color: transparent; 
  border: none;
  cursor: pointer;
  padding: 3px;
}